define([
  "lodash",
  "engine/stage/data-stage",
  "engine/protocol/download-socket-handler"
], function (_, DataStage, DownloadSocketHandler) {
  return DataStage.extend({
    stageName: "download",
    usePipeline: true,

    createSocketHandler: function(options) {
      return new DownloadSocketHandler(_.extend({}, options, {
        stage: this
      }));
    },

    _invokeFinishCallbacks: function() {
      this.callbacks.updateDownloadResult(this._result());
    }
  });
});
